"use client";

import React, { useState } from "react";
import Image from "next/image";
import logo from "../../public/Logo.png";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBars, faTimes } from "@fortawesome/free-solid-svg-icons";

const Header: React.FC = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const scrollToSection = (id: string) => {
    const section = document.getElementById(id);
    if (section) {
      section.scrollIntoView({ behavior: "smooth" });
      setIsMenuOpen(false); // Cerrar menú al hacer click en enlace
    }
  };

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  return (
    // Header fijo para que quede en la parte superior siempre
    <header
      id="screen-top"
      className="
        fixed top-0 left-0 w-full z-50 
        bg-transparent 
        text-white 
        px-8 py-4 
        flex items-center
      "
    >
      <div className="container mx-auto flex justify-between items-center">
        {/* Logo (clicable para volver a la sección "home") */}
        <div className="cursor-pointer" onClick={() => scrollToSection("home")}>
          <Image src={logo} alt="Logo Secure Core Consulting" />
        </div>

        {/* Ícono de hamburguesa (solo se muestra en móvil) */}
        <div className="max-sm:block hidden cursor-pointer" onClick={toggleMenu}>
          <FontAwesomeIcon icon={isMenuOpen ? faTimes : faBars} className="w-6 h-6" />
        </div>

        {/* Menú de navegación */}
        <nav
          className={`
            flex 
            space-x-6 
            max-sm:space-x-0 
            max-sm:flex-col 
            max-sm:items-center 
            max-sm:fixed 
            max-sm:top-[60px] 
            max-sm:left-0 
            max-sm:w-full 
            max-sm:bg-[#eb3752ba] 
            max-sm:z-50 
            max-sm:py-4
            ${isMenuOpen ? "block" : "max-sm:hidden"}
          `}
        >
          <a onClick={() => scrollToSection("about")} className="cursor-pointer hover:underline">
            ¿Quiénes somos?
          </a>
          <a onClick={() => scrollToSection("services")} className="cursor-pointer hover:underline">
            Servicios
          </a>
          <a onClick={() => scrollToSection("mission")} className="cursor-pointer hover:underline">
            Misión y Visión
          </a>
          <a onClick={() => scrollToSection("features")} className="cursor-pointer hover:underline">
            Valores Destacados
          </a>
          <a onClick={() => scrollToSection("contact")} className="cursor-pointer hover:underline">
            Contacto
          </a>
        </nav>
      </div>
    </header>
  );
};

export default Header;
